:root {
  --text-color: #434455;
  --subtext-color: #8e8f99;
  --hero-background: #2e2f42;
  --brand-color: #4d5ae5;
  --background-color: #fff;
  --light-background: #f4f4fd;
  --navy-blue: #2e2f42;
  --button-hover: #404bbf;
  --white: #fff;
  --cornflower: #e7e9fc;
  --slate: #434455;
  --ocean: #31d0aa;
  --light-slate: #8e8f99;
  --transition: 250ms cubic-bezier(.4, 0, .2, 1);
  --dairy: #fcfcfc;
}

body {
  background-color: #f4f4fd;
  background-image: url("background.f67e7a7f.jpg");
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  font-family: Roboto, sans-serif;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 50px;
  padding: 50px;
  display: flex;
}

.photo-card {
  min-width: 320px;
  background-color: var(--background-color);
  text-align: center;
  border: 1px solid #000;
  border-radius: 0 0 4px 4px;
  flex-basis: calc(20% - 9.6px);
  padding-bottom: 32px;
  line-height: 1.11;
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 2px 1px rgba(46, 47, 66, .08);
}

.photo-card img {
  width: 100%;
  height: 220px;
  object-fit: cover;
}

.form-style {
  height: 75px;
  background-color: #4d5ae5;
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
  display: flex;
}

.load {
  height: 30px;
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
  padding: 30px;
  display: flex;
}

.load-more {
  min-width: 169px;
  color: var(--white);
  background-color: #fff;
  background-color: var(--brand-color);
  letter-spacing: .04em;
  color: var(--white);
  cursor: pointer;
  transition: background-color var(--transition);
  border: none;
  border-radius: 4px;
  margin: 0 auto;
  padding: 16px 32px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.5;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.load-more:-webkit-any(:hover, :focus) {
  background-color: var(--button-hover);
}

.load-more:is(:hover, :focus) {
  background-color: var(--button-hover);
}

.search-form input {
  width: 250px;
  height: 40px;
  transition: border-color var(--transition);
  background-color: #fff;
  border: 1px solid rgba(46, 47, 66, .2);
  border-radius: 4px;
  outline: none;
  padding-left: 10px;
}

.search-form input:focus {
  border-color: var(--brand-color);
}

.search-form button {
  background-color: var(--cornflower);
  width: 100px;
  color: var(--white);
  letter-spacing: .04em;
  color: var(--navy-blue);
  cursor: pointer;
  transition: background-color var(--transition);
  border: none;
  border-radius: 4px;
  margin: 0 auto;
  padding: 10px 15px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.5;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.search-form button:-webkit-any(:hover, :focus) {
  background-color: var(--ocean);
}

.search-form button:is(:hover, :focus) {
  background-color: var(--ocean);
}

